<template>
  <div class="Daily">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>挂日常</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="80px"
        class="ruleForm"
      >
        <el-form-item label="天数" prop="days">
          <el-input v-model="ruleForm.days" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="包活动">
          <el-radio-group v-model="ruleForm.contain">
            <el-radio label="1">不包</el-radio>
            <el-radio label="1">包（不限棋子）</el-radio>
            <el-radio label="1">包（限棋子）</el-radio>
          </el-radio-group>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"
            >保存</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Daily",
  components: {},
  created() {},
  watch: {},
  data() {
    var validateDays = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入天数"));
      } else {
        callback();
      }
    };

    return {
      ruleForm: {
        days: "",
        contain: "",
      },
      rules: {
        days: [{ validator: validateDays, trigger: "blur" }],
      },
    };
  },

  methods: {
    /**
     * 提交表单
     */
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loginRequest();
        } else {
          this.$message.error("请检查输入！");
          return false;
        }
      });
    },
    /**
     * 登录请求
     */
    loginRequest() {
      this.$api.login(this.ruleForm).then((res) => {
        if (res.code === "200") {
          this.$message.success("成功！");
        } else {
          this.$message.error(res.message);
        }
      });
    },
  },
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}
.content {
  padding: 0px 100px 0 20px;
  .ruleForm {
    width: 400px;
  }
}
</style>
  