<template>
  <div class="Order">
    <!-- 添加订单弹窗 -->
    <el-dialog title="添加订单信息" fullscreen width="500px" :visible.sync="dialogAddFormVisible">
      <el-form :model="addOrderForm">
        <el-form-item label="订单编号" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.id" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="登录账号" :label-width="formLabelWidth" required>
          <el-input v-model="addOrderForm.playerAccount" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="登录密码" :label-width="formLabelWidth" required>
          <el-input v-model="addOrderForm.playerEncryptionCode" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="渠道" :label-width="formLabelWidth">
          <el-select v-model="addOrderForm.playerChannel" placeholder="请选择" width="100%">
            <el-option v-for="item in playerChannelOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="AP恢复" :label-width="formLabelWidth">
            <el-checkbox label="果实" v-model="addOrderForm.fruitEnabled"></el-checkbox>
            <el-checkbox label="圣晶石" v-model="addOrderForm.rockEnabled"></el-checkbox>
        </el-form-item>

        <el-form-item label="不动队伍" :label-width="formLabelWidth">
          <el-radio-group v-model="addOrderForm.teamOnHold">
            <el-radio label="TRUE">动</el-radio>
            <el-radio label="FALSE">不动</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="代练内容" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.boostingContent" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="订单金额" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.orderAmount" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="联系方式" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.playerContact" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogAddFormVisible = false">取消</el-button>
        <el-button type="primary" @click="handleAddOrder">提交</el-button>
      </div>
    </el-dialog>
    <!-- 修改订单弹窗 -->
    <el-dialog title="修改订单信息" fullscreen width="500px" :visible.sync="dialogModifyFormVisible">
      <el-form :model="addOrderForm">
        
        <el-form-item label="订单编号" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.orderNumber" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="登录账号" :label-width="formLabelWidth" required>
          <el-input v-model="addOrderForm.playerAccount" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="登录密码" :label-width="formLabelWidth" required>
          <el-input v-model="addOrderForm.playerEncryptionCode" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="渠道" :label-width="formLabelWidth">
          <el-select v-model="addOrderForm.playerChannel" placeholder="请选择" width="100%">
            <el-option v-for="item in playerChannelOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="AP恢复" :label-width="formLabelWidth">
          <el-checkbox label="果实" v-model="addOrderForm.fruitEnabled"></el-checkbox>
            <el-checkbox label="圣晶石" v-model="addOrderForm.rockEnabled"></el-checkbox>
        </el-form-item>

        <el-form-item label="不动队伍" :label-width="formLabelWidth">
          <el-radio-group v-model="addOrderForm.teamOnHold">
            <el-radio label="TRUE">动</el-radio>
            <el-radio label="FALSE">不动</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="代练内容" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.boostingContent" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="订单金额" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.orderAmount" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="联系方式" :label-width="formLabelWidth">
          <el-input v-model="addOrderForm.playerContact" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogModifyFormVisible = false">取消</el-button>
        <el-button type="primary" @click="handleModifyOrder">提交</el-button>
      </div>
    </el-dialog>
    <div class="header"><span>FGO后台管理系统</span></div>
    <div class="content">
      <div class="aside">
        <el-menu :default-active="activeNavName" class="el-menu-vertical" background-color="#545c64" text-color="#fff"
          active-text-color="#ffd04b">
          <el-submenu index="1">
            <template slot="title">
              <span>订单管理</span>
            </template>
            <el-menu-item v-for="(item, i) in navList" :key="i" :index="item.name" @click="selectNav(item.name)">
              {{ item.navItem }}
            </el-menu-item>
          </el-submenu>
        </el-menu>
      </div>
      <div class="content-right">
        <div class="content-header">
          <!-- 状态筛选 -->
          <div class="select-status">
            <span class="select-label">状态：</span>
            <el-select size="mini" class="select-status-input" v-model="activeNavName" placeholder="请选择">
              <el-option v-for="item in selectStatusOptions" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
          <!-- 操作 -->
          <div class="order-option">
            <el-button size="mini" type="primary" icon="el-icon-minus" @click="handleMidSettle">中途结算</el-button>
            <el-button size="mini" type="primary" icon="el-icon-check" @click="handleSubmitSettle">交单结算</el-button>
            <el-button size="mini" type="primary" icon="el-icon-plus" @click="handleOrderAddDialog">添加订单</el-button>
          </div>

          <!-- 搜索顶栏 -->
          <div class="search">
            <el-input class="search-input" v-model="searchValue" size="mini" placeholder="输入关键字搜索" />
            <el-button size="mini" class="search-button" type="primary" icon="el-icon-search"
              @click="handleSearchOrder">搜索</el-button>
          </div>
        </div>
        <div class="content-inner">
          <!-- 订单列表 -->
          <div class="OrderList">
            <el-table stripe border size="mini" :data="tableData" style="width: 100%;" :max-height="tableMaxHeight"
              @selection-change="handleSelectionChange">
              <el-table-column fixed="left" type="selection" width="55">
              </el-table-column>
              <el-table-column fixed="left" prop="id" label="id">
              </el-table-column>
              <el-table-column fixed="left" prop="orderNumber" label="订单编号">
              </el-table-column>
              <el-table-column v-for="(item, i) in tableHeader" :prop="item.key" :label="item.name" :key="i">
              </el-table-column>
              <el-table-column fixed="right" label="操作" width="220">
                <template slot-scope="scope">
                  <el-tooltip :content="scope.row.status" placement="top">
                    <el-switch class="switch-button" v-model="scope.row.status" active-color="#13ce66"
                      inactive-color="#ff4949" active-value="RUNNING" inactive-value="STOPPED"
                      @change="handleChangeOrderStatus(scope.row)">
                    </el-switch>
                  </el-tooltip>
                  <el-button size="mini" type="primary" @click="handleJumpBusiness(scope.row)">业务
                  </el-button>
                  <el-button size="mini" type="primary" @click="handleOrderModifyDialog(scope.row)">修改
                  </el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </div>
        <div class="content-footer">
          <!-- 分页器 -->
          <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
            :current-page="orderListParams.pageNum" :page-sizes="[10, 20, 30, 40]" :page-size="orderListParams.pageSize"
            layout="total, sizes, prev, pager, next, jumper" :total="paginationInfo.total">
          </el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Order",
  components: {},
  created() {
    if (window.innerHeight < 900) {
      this.tableMaxHeight = "500px";
    } else if (window.innerHeight >= 900) {
      this.tableMaxHeight = "700px";
    }
    this.navList.forEach((item) => {
      this.selectStatusOptions.push({
        value: item.name,
        label: item.navItem,
      });
    });
  },
  watch: {
    activeNavName: {
      handler(newVal) {
        this.updateOrderListByStatus(newVal);
      },
      immediate: true,
    },
  },
  data() {
    return {
      /**
       * const 菜单列表
       */
      navList: [
        { name: "INIT", navItem: "全部" },

        { name: "UNFINISHED", navItem: "未完成订单" },
        { name: "UNFINIISHED_EVENT_DONE", navItem: "活动结束未完成" },
        { name: "ABNORMAL", navItem: "异常订单" },
        { name: "PASSWORD_EXCEPTION", navItem: "密码问题订单" },
        { name: "OUT_OF_ACTIVE_POWER", navItem: "AP不足订单" },
        { name: "FINISHED", navItem: "已完成订单" },
        { name: "SETTLED", navItem: "已处理订单" },
        { name: "BLACK_LIST", navItem: "黑名单" },
      ],
      /**
       * const 订单状态筛选条目
       */
      selectStatusOptions: [
        {
          value: "INIT",
          label: "全部",
        },
      ],
      /**
       * 渠道选择条目
       */
      playerChannelOptions: [
        {
          value: "iOS",
          label: "iOS",
        },
        {
          value: "Android",
          label: "Android",
        }
      ],
      /**
       * var 当前菜单
       */
      activeNavName: "INIT",
      tableMaxHeight: "",
      tableHeader: [
        {
          key: "playerAccount",
          name: "账号",
        },
        {
          key: "playerEncryptionCode",
          name: "密码",
        },
        {
          key: "playerChannel",
          name: "渠道",
        },
        {
          key: "createTime",
          name: "录入时间",
        },
        {
          key: "recentlyLoginTime",
          name: "最近登录",
        },
        {
          key: "boostingContent",
          name: "代练内容",
        },
        {
          key: "rockFruitAtStart",
          name: "开始石头果实",
        },
        {
          key: "rockFruitAtCurrent",
          name: "现在石头果实",
        },
        {
          key: "battelCount",
          name: "战斗次数",
        },
        {
          key: "actionPowerRecovery",
          name: "AP恢复",
        },
        {
          key: "status",
          name: "状态",
        },
        {
          key: "orderNumber",
          name: "金额",
        },
        {
          key: "orderAmount",
          name: "已结算",
        },
        {
          key: "playerContact",
          name: "联系方式",
        },
      ],
      /**
       * var 搜索内容
       */
      searchValue: "",
      /**
       * var 当前页码
       */
      paginationInfo: {
        total: 0,
        totalPage: 0,
      },

      /**
       * var 获取订单列表请求参数
       */
      orderListParams: {
        orderNumber: "",
        status: "INIT", // UNFINISHED,UNFINIISHED_EVENT_DONE,ABNORMAL,PASSWORD_EXCEPTION,OUT_OF_ACTIVE_POWER,FINISHED,BLACK_LIST,SETTLED
        pageNum: 1,
        queryStr: "",
        orderBy: "ASC",
        pageSize: 10,
      },
      /**
       * 添加订单
       */
      dialogAddFormVisible: false,
      formLabelWidth: "120px",
      addOrderForm: {
        id: "",
        orderNumber: "",
        playerAccount: "",
        playerEncryptionCode: "",
        playerChannel: "1",
        fruitEnabled: false,
        rockEnabled: false,
        teamOnHold: "N",
        boostingContent: " ",
        orderAmount: "0",
        playerContact: "",
      },
      /**
       * 修改订单
       */
      dialogModifyFormVisible: false,

      /**
       * var 获取订单列表响应数据
       */
      tableData: [

      ],
      /**
       * 多选
       */
      multipleSelection: [],
    };
  },

  methods: {
    /**
     * 菜单选取
     */
    selectNav(index) {
      this.activeNavName = index;
    },

    /**
     * 路由跳转
     */
    jumpUrl(path) {
      this.$router.push(path);
    },

    // 事件绑定回调函数
    /**
     * 1. 更新订单列表
     */

    updateOrderList() {
      this.$api.getOrder(this.orderListParams).then((res) => {
        const body = res.data;
        if (body.code == "200") {

          this.$message.success("成功！");
          let data = body.data;
          // }
          //           "id":34,
          //           "orderNumber":"kkkk",
          //           "playerAccount":"12346",
          //           "playerEncryptionCode":"123",
          //           "playerChannel":"iOS",
          //           "teamOnHold":"TRUE",
          //           "boostingContent":"sss",
          //           "orderAmount":0.000,
          //           "playerContact":"13633333",
          //           "status":"INIT",
          //           "createTime":"2022-10-31T16:00:06.000+00:00",
          //           "recentlyLoginTime":null,
          //           "rockAtStart":0,
          //           "fruitAtStart":0,
          //           "fruitAtCurrent":0,
          //           "rockAtCurrent":0,
          //           "battleCount":0,
          //           "exceptionMessage":" ",
          //           "fruitEnabled":"TRUE",
          //           "rockEnabled":"FALSE",
          //           "boostingTask":" ",
          //           "boostingProgress":null,
          //           "accountSituation":null},
          let listBody = [];
          for (let i = 0; i < data.list.length; i++) {
            let item = data.list[i];
            let listItem = {
              id: item.id,
              orderNumber: item.orderNumber,
              playerAccount: item.playerAccount,
              playerEncryptionCode: item.playerEncryptionCode,
              playerChannel: item.playerChannel,
              actionPowerRecovery: "",
              teamOnHold: item.teamOnHold,
              boostingContent: item.boostingContent,
              orderAmount: item.orderAmount,
              playerContact: item.playerContact,
              status: item.status,
              createTime: item.createTime,
              recentlyLoginTime: item.recentlyLoginTime,
              rockFruitAtStart: item.fruitAtStart,
              rockFruitAtCurrent: item.fruitAtCurrent,
              battelCount: item.battleCount,
              exceptionMessage: item.exceptionMessage,
            };
            if(item.fruitEnabled == "TRUE") {
              listItem.actionPowerRecovery += "果实 ";
            }
            if(item.rockEnabled == "TRUE") {
              listItem.actionPowerRecovery += "圣晶石 ";
            }
            if(item.fruitEnabled == "FALSE" && item.rockEnabled == "FALSE") {
              listItem.actionPowerRecovery = "自然恢复";
            }
            listBody.push(listItem);
          }
          this.tableData = listBody;
          this.paginationInfo = {
            total: data.total,
            totalPage: data.pages,
          }

        } else {
          this.$message.error(body.message);
        }
      });
    },
    // 根据订单状态更新订单
    updateOrderListByStatus(status) {

      this.orderListParams.status = status;
      this.updateOrderList();
    },

    /**
     * 2. 订单状态筛选
     */

    /**
     * 3. 中途结算
     */
    // 多选
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleMidSettle() {
      this.$api.midSettle(this.multipleSelection).then((res) => {
        const body = res.data;
        if (body.code == "200") {
          this.$message.success("成功！");
        } else {
          this.$message.error(body.message);
        }
      });
    },
    /**
     * 4. 交单结算
     */
    handleSubmitSettle() {
      this.$api.submitSettle(this.multipleSelection).then((res) => {
        const body = res.data;

        if (body.code == "200") {
          this.$message.success("成功！");
        } else {
          this.$message.error(body.message);
        }
      });
    },
    /**
     * 5. 添加订单
     */
    handleOrderAddDialog() {
      this.clearAddOrder();
      this.dialogAddFormVisible = true;
    },
    handleAddOrder() {

      let params = {
        orderNumber: this.addOrderForm.orderNumber,
        playerAccount: this.addOrderForm.playerAccount,
        playerEncryptionCode: this.addOrderForm.playerEncryptionCode,
        playerChannel: this.addOrderForm.playerChannel,
        fruitEnabled: this.addOrderForm.fruitEnabled ? "TRUE" : "FALSE",
        rockEnabled: this.addOrderForm.rockEnabled ? "TRUE" : "FALSE",
        teamOnHold: this.addOrderForm.rockEnabled ? "TRUE" : "FALSE",
        boostingContent: this.addOrderForm.boostingContent,
        orderAmount: this.addOrderForm.orderAmount,
        playerContact: this.addOrderForm.playerContact,
      }
      
      this.$api.addOrder(params).then((res) => {
        const body = res.data;

        if (body.code == "200") {
          this.$message.success("添加成功！");
          this.dialogAddFormVisible = false;
          this.clearAddOrder();
          this.updateOrderList();
        } else {
          this.$message.error(body.message);
          this.clearAddOrder();
        }
      });
    },
    clearAddOrder() {
      this.addOrderForm = {
        id: "",
        orderNumber: "",
        playerAccount: "",
        playerEncryptionCode: "",
        playerChannel: "iOS",
        fruitEnabled: false,
        rockEnabled: false,
        teamOnHold: "FALSE",
        boostingContent: " ",
        orderAmount: "0",
        playerContact: "",
      };
    },
    /**
     * 9. 修改订单
     */
    handleOrderModifyDialog(row) {
      this.dialogModifyFormVisible = true;

      this.addOrderForm = {
        id: row.id,
        orderNumber: row.orderNumber,
        playerAccount: row.playerAccount,
        playerEncryptionCode: row.playerEncryptionCode,
        playerChannel: row.playerChannel,
        fruitEnabled: "",
        rockEnabled: "",
        teamOnHold: row.teamOnHold,
        boostingContent: row.boostingContent,
        orderAmount: row.orderAmount,
        playerContact: row.playerContact,
      };
      
      const ap = row.actionPowerRecovery.split(" ");
      for(let i = 0; i < ap.length; i ++) {
        if(ap[i] == "") continue;
        if(ap[i] == "果实") {
          this.addOrderForm.fruitEnabled = true;
        } else if(ap[i] == "圣晶石") {
          this.addOrderForm.rockEnabled = true;
        } else {
          this.addOrderForm.rockEnabled = false;
          this.addOrderForm.fruitEnabled = false;
        }
      }
    },
    handleModifyOrder() {
      let params = {
        id: this.addOrderForm.id,
        orderNumber: this.addOrderForm.orderNumber,
        playerAccount: this.addOrderForm.playerAccount,
        playerEncryptionCode: this.addOrderForm.playerEncryptionCode,
        playerChannel: this.addOrderForm.playerChannel,
        fruitEnabled: this.addOrderForm.fruitEnabled ? "TRUE" : "FALSE",
        rockEnabled: this.addOrderForm.rockEnabled ? "TRUE" : "FALSE",
        teamOnHold: this.addOrderForm.rockEnabled ? "TRUE" : "FALSE",
        boostingContent: this.addOrderForm.boostingContent,
        orderAmount: this.addOrderForm.orderAmount,
        playerContact: this.addOrderForm.playerContact,
      };
      this.$api.modifyOrder(params).then((res) => {
        const body = res.data;

        if (body.code == "200") {
          this.$message.success("成功！");
          this.dialogModifyFormVisible = false;
          this.clearAddOrder();
          this.updateOrderList();
        } else {
          this.$message.error(body.message);
          this.clearAddOrder();
        }
      });

    },

    /**
     * 6. 搜索订单
     */
    handleSearchOrder() {
      this.orderListParams.queryStr = this.searchValue;
      this.orderListParams.pageNum  = 1;
      this.orderListParams.pageSize  = 10;
      this.updateOrderList();
      
    },
    /**
     * 7. 更改订单状态
     */
    handleChangeOrderStatus(row) {
      const params = {
        orderId: row.id,
        orderStatus: row.status,
      };
      this.$api.changeOrderStatus(params).then((res) => {
        const body = res.data;

        if (body.code == "200") {
          this.$message.success("成功！");
        } else {
          row.status = "STOPPED";
          this.$message.error(body.message);
        }
      });

    },
    /**
     * 8. 跳转业务页面
     */
    // 跳转订单项业务页面
    handleJumpBusiness(row) {
      const routeData = this.$router.resolve({
        path: '/task',
        query: {
          id: row.id
        }
      });
      window.open(routeData.href, '_blank');
    },
    /**
     * 10. 分页获取
     */
    handleSizeChange(val) {
      this.orderListParams.pageSize = val;
      this.updateOrderList();
    },
    handleCurrentChange(val) {
      this.orderListParams.pageNum = val;

      this.updateOrderList();

    },



  },
};
</script>

<style scoped lang="less">
