<template>
  <div class="Purchase">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>购买关卡</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="80px"
        class="ruleForm"
      >
        <el-form-item label="关卡" :label-width="formLabelWidth">
          <el-select v-model="ruleForm.level" placeholder="请选择">
            <el-option v-for="item in levelOptions" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="代练内容">
          <el-radio-group v-model="ruleForm.content">
            <el-radio label="1">活动毕业</el-radio>
            <el-radio label="2">活动毕业（不搬棋子）</el-radio>
            <el-radio label="3">活动通关</el-radio>
            <el-radio label="4">五宝满破</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm"
            >保存</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Purchase",
  components: {},
  created() {

  },
  watch: {

  },
  data() {
    return {
      ruleForm: {
        level: "",
        content: "",
      },
      levelOptions: [
        {
          value: "all",
          label: "全部",
        },
      ]
    };
  },

  methods: {
    /**
     * 提交表单
     */
     submitForm() {
          this.loginRequest();
    },
    /**
     * 登录请求
     */
    loginRequest() {
      this.$api.login(this.ruleForm).then(
        (res) => {
          if (res.code === "200") {
            this.$message.success("成功！");

          } else {
            this.$message.error(res.message);
          }
        }
        
      );
    },
    
  }
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}
.content {
  padding: 0px 100px 0 20px;
  .ruleForm {
  width: 400px;

  }
}
</style>
  