<template>
  <div class="Activity">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>挂活动</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="80px"
        class="ruleForm"
      >
       <el-form-item label="活动">
          <el-radio-group v-model="ruleForm.activity">
            <el-radio label="">恰赫季斯·万圣节·三部曲</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="代练内容">
          <el-radio-group v-model="ruleForm.content">
            <el-radio label="1">机械伊丽亲①号机</el-radio>
            <el-radio label="2">机械伊丽亲②号机</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="池子数" prop="pool" required>
          <el-input v-model="ruleForm.pool" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"
            >保存</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Activity",
  components: {},
  created() {

  },
  watch: {

  },
  data() {
    var validatePool = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入"));
      } else {
        callback();
      }
    };
    

    return {
      ruleForm: {
        activity: "",
        content: "",
        pool: "",
      },
      rules: {
        pool: [{ validator: validatePool, trigger: "blur" }],
      },
      formLabelWidth: "80px",
    };
  },

  methods: {
    /**
     * 提交表单
     */
     submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loginRequest();
        } else {
          this.$message.error("请检查输入！");
          return false;
        }
      });
    },
    /**
     * 登录请求
     */
    loginRequest() {
      this.$api.login(this.ruleForm).then(
        (res) => {
          if (res.code === "200") {
            this.$message.success("成功！");

          } else {
            this.$message.error(res.message);
          }
        }
        
      );
    },
    
  }
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}
.content {
  padding: 0px 100px 0 20px;
  .ruleForm {
  width: 400px;

  }
}
</style>
  