<template>
  <div class="Balls">
    <div class="header">
      <el-breadcrumb class="bread" separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>业务管理</el-breadcrumb-item>
        <el-breadcrumb-item>搓丸子</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="content">
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="80px"
        class="ruleForm"
      >
        <el-form-item label="丸子数" prop="balls" :label-width="formLabelWidth" required>
          <el-input v-model="ruleForm.balls" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item :label-width="formLabelWidth">
          <el-button type="primary" @click="submitForm('ruleForm')"
            >保存</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
  
<script>
export default {
  name: "Balls",
  components: {},
  created() {

  },
  watch: {

  },
  data() {
    var validateBalls = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入丸子数"));
      } else {
        callback();
      }
    };
    

    return {
      ruleForm: {
        balls: "",
      },
      rules: {
        balls: [{ validator: validateBalls, trigger: "blur" }],
      },
      formLabelWidth: "80px",

    };
  },

  methods: {
    /**
     * 提交表单
     */
     submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loginRequest();
        } else {
          this.$message.error("请检查输入！");
          return false;
        }
      });
    },
    /**
     * 登录请求
     */
    loginRequest() {
      this.$api.login(this.ruleForm).then(
        (res) => {
          if (res.code === "200") {
            this.$message.success("成功！");

          } else {
            this.$message.error(res.message);
          }
        }
        
      );
    },
    
  }
};
</script>
  
<style scoped lang="less">
.header {
  padding: 20px 100px 0 20px;
  height: 50px;
}
.content {
  padding: 0px 100px 0 20px;
  .ruleForm {
  width: 400px;

  }
}
</style>
  