<template>
  <div class="Register">
    <div class="formBox">
      <h1>注册</h1>
      <el-form
        :model="ruleForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="40px"
        class="ruleForm"
      >
        <el-form-item label="账号" prop="account">
          <el-input v-model="ruleForm.account" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="pass">
          <el-input
            type="password"
            v-model="ruleForm.pass"
            autocomplete="off"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">登录/注册</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  name: "Register",
  props: {},
  data() {
    var validateAccount = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入用户名"));
      } else {
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };

    return {
      ruleForm: {
        account: "",
        pass: "",
      },
      rules: {
        account: [{ validator: validateAccount, trigger: "blur" }],
        pass: [{ validator: validatePass, trigger: "blur" }],
      },
    };
  },
  methods: {
    /**
     * 提交表单
     */
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.registerRequest();
        } else {
          this.$message.error("请检查用户名和密码！");
          return false;
        }
      });
    },
    /**
     * 注册并登录请求
     */
    registerRequest() {
      this.$api.register(this.ruleForm).then(
        (res) => {
          if (res.code === "200") {
            this.$message.success("注册成功！");
            this.loginRequest();
          } else {
            this.$message.error(res.message);
          }
        }
      )
    },
    /**
     * 登录请求
     */
    loginRequest() {
      this.$api.login(this.ruleForm).then(
        (res) => {
          if (res.code === "200") {
            const data = res.data;
            const token = res.headers["user-session"];
            this.$store.commit('setToken', token);
            this.$store.commit('setUserName', data.userName);
            this.$store.commit('setUserRole', data.userRole);

            this.$message.success("登录成功！");
            this.jumpUrl("/order");

          } else {
            this.$message.error(res.message);
          }
        }
      );
    },
    /**
     * 路由跳转
     */
    jumpUrl(url) {
      this.$router.push(url);
    },
  },
};
</script>

<style scoped lang="less">
.Register {
  display: flex;
  justify-content: center;
}
.formBox {
  margin: 200px;
  padding: 20px;
  width: 500px;
  border: 1px solid #eee;
  border-radius: 5px;

  h1 {
    margin: 10px 0;
  }
}
</style>